/* --------- responsive mixins --------- */

html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
nav,
output,
ruby,
section,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html,
body {
  width: 100%;
  height: 100%;
  font-size: 14px;
}

input {
  outline-color: #b12167;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

ul {
  padding: 0;
  margin: 0;
}

ul li {
  padding: 0;
  margin: 0;
  list-style: none outside none;
}

button,
button:active,
button:focus {
  outline: none;
}

input::-webkit-input-placeholder {
  color: #7c6c69;
}

input::-moz-placeholder {
  color: #7c6c69;
}

/* Firefox 19 */

input:-moz-placeholder {
  color: #7c6c69;
}

/* Firefox 18 */

input:-ms-input-placeholder {
  color: #7c6c69;
}

p {
  padding: 0;
  margin: 0;
}

a {
  outline: none !important;
  text-decoration: none;
  color: black;
}

.main {
  position: relative;
  min-height: 100%;
  height: auto !important;
  height: 100%;
  width: 100%;
}

* {
  -webkit-box-sizing: border-box;
  -ms-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-tap-highlight-color: transparent;
}

img {
  -webkit-user-select: none !important;
  -moz-user-select: none !important;
  -ms-user-select: none !important;
  user-select: none !important;
}

/* header */

.header {
  background-color: #27273e;
  min-height: 76px;
}

.box-top {
  margin: 0px auto;
  max-width: 1100px;
  display: flex;
}

.header .box {
  padding-top: 10px;
  justify-content: space-between;
}

.header__logo {
  margin: 10px 0px;
  width: 99px;
  height: 51px;
  background: url("/img/logo-fig.png") no-repeat;
}

.header__contact {
  margin: 15px 0px 15px  auto;
  display: flex;
  flex-direction: column;
}

.header__contacts-number {
  font-family: "GothamProBold";
  font-size: 21px;
  margin-left: auto;
  color: white;
}

.header__contacts-chart {
  margin-left: auto;
  color: #b12167;
  font-family: "GothamProBold";
  font-size: 14px;
  text-decoration: underline;
  text-decoration: 1px solid #b12167;
}

.pop-up__window {
  position: absolute;
  width: 160px;
  height: 70px;
  background-color: white;
  font-size: 16px;
  font-weight: 600;
  color: #221f1f;
  padding: 10px;
  display: none;
}

.header__contacts-chart:hover .pop-up__window {
  display: block;
  margin: 30px -60px;
}

.pop-up__window p:first-child {
  color: #ec1e79;
}

/* aside */

.wrsrcer {
  max-width: 1100px;
  margin: 0px auto;
  display: flex;
}

.section__left {
  padding-top: 20px;
  min-width: 150px;
  min-height: 100%;
  display: flex;
}

.section__left-pic {
  margin: 20px;
}

.section__left-link {
  margin: 17px;
  font-size: 20px;
  color: #ec1e79;
}

.section__left-link:last-child {
  color: black;
}

.content_menu {
  display: flex;
  flex-direction: column;
  padding-top: 20px;
}

.content_menu-link {
  display: flex;
  align-items: center;
  text-decoration: none;
  margin: 15px;
}

.content_menu-link:hover > .content_menu-text {
  color: #000;
}

.content_menu-pic {
  width: 29px;
  height: 29px;
  margin-right: 14px;
  transition: .3s;
}

.content_menu-text {
  font-weight: 700;
  font-size: 14px;
  color: #ff006a;
  transition: .3s;
}

/* bread-crumd */

.section__right {
  display: flex;
  flex-direction: column;
  align-items: space-between;
  padding-top: 20px;
}

.breadcrumbs {
  padding-top: 30px;
  max-height: 60px;
}

.breadcrumbs__item {
  font-size: 16px;
  color: #ec1e79;
}

.breadcremd__text {
  font-size: 16px;
  color: black;
}

.breadcrumbs__item:last-child {
  color: black;
}

.heading {
  font-weight: 900;
  display: block;
  font-size: 50px;
  font-family: "GothamProBold";
}

/* service */

.service__item {
  margin: 20px;
  display: flex;
}

.service__flex {
  display: flex;
  flex-direction: column;
  margin-left: 15px;
  max-width: 600px;
}

.service__item-pic {
  width: 160px;
  height: 160px;
  margin: 10px;
  flex-shrink: 0;
}

.service__item-img1 {
  background: url(../img/vcvb1.png) no-repeat;
}

.service__item-img2 {
  background: url(../img/fdfd2.png) no-repeat;
}

.service__item-img3 {
  background: url(../img/logo-fig3.png) no-repeat;
  background-position: 30px 45px;
  background-color: #f5f5f5;
  border-radius: 100px;
}

.service__item-img4 {
  background: url(../img/swcnamirzyc-danielle-marroquin4.png) no-repeat;
}

.service__date {
  font-size: 14px;
  color: #3d3b3b;
}

.service__name {
  font-size: 14px;
  font-weight: 700;
}

.service__text {
  font-size: 18px;
  color: #3d3b3b;
}

/* slider */

.slider {
  display: flex;
  font-size: 14px;
  text-align: center;
  margin: 0 auto;
}

.slider-number {
  text-align: center;
  padding-top: 5px;
  margin-right: 10px;
}

.slider-extend {
  margin: 0px 4px;
  padding-top: 5px;
}

.slider-extend:hover {
  color: #ec1e79;
  background-color: #27273e;
  width: 25px;
  height: 25px;
}

.fitback {
  max-width: 700px;
  margin: 75px auto;
  background-color: #f5f5f5;
  padding: 50px 70px;
}

.fitback p {
  margin: 15px 0px 5px 0px;
}

.fitback__flex {
  flex-wrap: wrap;
  display: flex;
  margin-top: 15px;
}

.fitback__input {
  width: 50%;
}

.fitback p {
  font-size: 14px;
}

.fitback__input-left {
  width: 49%;
}

.fitback__input-right {
  width: 49%;
  margin-left: 2%;
  white-space: nowrap;
}

.fitback__input-name {
  width: 100%;
  height: 40px;
  padding: 10px;
}

.fitback__input-VK {
  width: 100%;
  height: 40px;
  padding: 10px;
}

.fitback__input-text {
  width: 100%;
  height: 90px;
  padding: 10px 10px 70px 10px;
}

.fitback__radio {
  margin-top: 15px;
  color: #ec1e79;
}

.fitback__input-button {
  position: relative;
  bottom: 25px;
  margin-left: auto;
  width: 140px;
  height: 45px;
  background-color: #27273e;
  border: none;
  color: white;
  outline: none;
  cursor: pointer;
}

.fitback__flex {
  display: flex;
}

.footer__fix {
  margin: 0px auto;
  max-width: 1100px;
  display: flex;
  justify-content: space-between;
}

.footer {
  min-height: 77px;
  background: #27273e;
  clear: both;
}

.footer .box {
  display: block;
}

.footer__left span {
  font-weight: 700;
}

.footer__span {
  border-bottom: 1px solid #5d5d6e;
  color: white;
}

.footer__left {
  word-spacing: 5px;
  float: left;
  color: #eaeaec;
  font-size: 14px;
  padding: 14px;
}

.footer__center {
  align-self: center;
  padding: 14px 0;
}

.footer__right {
  position: relative;
  float: left;
  text-align: right;
  padding: 15px 50px 10px 10px;
}

.menu__text {
  color: #ffffff;
  font-family: 'Gothambold';
  margin: 20px 20px 0 0;
  position: absolute;
  right: 2px;
  top: -8px;
  white-space: nowrap;
}

.menu__cup {
  display: inline-block;
  width: 25px;
  height: 25px;
  margin: 5px;
}

@media screen and (max-width: 800px) {
  .service__item {
    text-align: center;
    align-items: center;
    flex-direction: column;
  }
}

@media screen and (max-width: 690px) {
  .content_menu {
    margin: 0px auto;
    flex-wrap: wrap;
    flex-direction: row;
  }

  .wrsrcer {
    display: block;
    margin: 20px;
  }
}

@media screen and (max-width: 675px) {
  .fitback {
    padding: 10px 20px;
  }
}

@media screen and (max-width: 475px) {
  .fitback__input-right {
    margin: 0px;
    width: 100%;
  }

  .fitback__input-left {
    margin: 0px;
    width: 100%;
  }

  .fitback__input-button {
    bottom: 10px;
  }
}